
import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { IAppConfig } from '../models/app-config.model';
import { environment } from '../../environments/environment';
import { tap, catchError, finalize } from 'rxjs/operators';
import { HttpErrorHandler, HandleError } from './http-error-handler.service';
import { throwError, Observable, Subject, of } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class ProjectConfigService {
    private handleError: HandleError;
    private _serviceName: string = 'ProjectConfigService';

    static settings: IAppConfig = environment;
    static loaded: boolean = false;

    constructor(private http: HttpClient,
                private httpErrorHandler: HttpErrorHandler) {
        this.handleError = httpErrorHandler.createHandleError(this._serviceName);
    }

    load(): Observable<IAppConfig> {
        if (ProjectConfigService.loaded)
            return of(ProjectConfigService.settings);
        else
            return this.http.get<IAppConfig>(environment.configFileURL)
                    .pipe(
                        tap((data) => {
                            this.log(`${this._serviceName}/load`, data);
                            ProjectConfigService.overrideStaticConfiguration(<IAppConfig>data);
                            ProjectConfigService.loaded = true;
                            return ProjectConfigService.settings;
                        })
                    );
                

        /*return this.http.get<IAppConfig>(jsonFile)
            .pipe(
                tap(
                    data => {
                        this.log(`${this._serviceName}/load`, data)
                        ProjectConfigService.overrideStaticConfiguration(<IAppConfig>data);
                    }
                    , error => {
                        console.log(`Could not load file '${jsonFile}': ${JSON.stringify(error)}`);
                        this.logError(`${this._serviceName}/load`, error)
                    }
                ),
                catchError(this.handleError<IAppConfig>('Carregar itens', environment)),
                finalize(() => {
                    ProjectConfigService.loaded = true;
                })
            );
*/
    }

    private static overrideStaticConfiguration(configObject: IAppConfig) {
        ProjectConfigService.settings = configObject;
        for (let key in configObject) {
            if (configObject.hasOwnProperty(key)) {
                environment[key] = configObject[key];
            }
        }
    }

    /**
	 * Handle Http operation that succeded.
	 * @param operation - name of the operation that succeded
	 * @param result - Result value to return as the observable
	 */
    private log<T>(operation: string, result: T) {
        console.log(`${operation} succeded`);

        // Let the app keep running by returning an empty result.
        //return of(result as T);
        return result;
    }

	/**
	 * Handle Http operation that failed.
	 * Let the app continue.
	 * @param operation - name of the operation that failed
	 * @param result - optional value to return as the observable result
	 */
    private logError(operation: string, result: HttpErrorResponse) {
        // TODO: send the error to remote logging infrastructure
        if (result.error instanceof ErrorEvent) {
            // A client-side or network error occurred. Handle it accordingly.
            console.error('An error occurred:', result.error.message);
        } else {
            // The backend returned an unsuccessful response code.
            // The response body may contain clues as to what went wrong,
            console.error(
                `Backend returned code ${result.status}, ` +
                `body was: ${result.error}`);
        }

        console.log(`${operation} failed: ${result.message}`);

        // Let the app keep running by returning an empty result.
        //return of(result as T);
        return throwError(
            'Something bad happened; please try again later.');
    }

}