import { Injectable } from '@angular/core';
import { NgbDateStruct, NgbDateParserFormatter, NgbDate } from '@ng-bootstrap/ng-bootstrap';
import { TextFormatter } from './TextFormatters';

@Injectable({
    providedIn: 'root'
})
export class BrDateFormatter extends NgbDateParserFormatter {
    parse(value: string): NgbDateStruct {
        if (value == null)
            return null;
        //console.log(value);
        let values = value.split('/');
        const date = new NgbDate(parseInt(values[2]).valueOf(), parseInt(values[1]).valueOf(), parseInt(values[0]).valueOf());
        return date;
    }
    format(date: NgbDateStruct): string {
        if (date == null)
            return null;
        console.log(`${TextFormatter.preencherZerosEsquerda(date.day.toString(),2)}/${TextFormatter.preencherZerosEsquerda(date.month.toString(),2)}/${TextFormatter.preencherZerosEsquerda(date.year.toString(),4)}`);
        return `${TextFormatter.preencherZerosEsquerda(date.day.toString(),2)}/${TextFormatter.preencherZerosEsquerda(date.month.toString(),2)}/${TextFormatter.preencherZerosEsquerda(date.year.toString(),4)}`;
        //console.log(`${date.year}${date.month}${date.day}`);
        //return `${date.year}${date.month}${date.day}`;
    }
}
