import {Injectable} from '@angular/core';
import {Headers, Http, Response} from '@angular/http';
import { HttpClient, HttpHeaders, HttpErrorResponse, HttpResponse } from '@angular/common/http';
import {JwtService} from './jwt.service';
import { BehaviorSubject, Observable, throwError } from 'rxjs';
import { tap, catchError, switchMap, take } from 'rxjs/operators';
import { HandleError, HttpErrorHandler } from './http-error-handler.service';
import { ProjectConfigService } from './project.config.service';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Accept': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})
export class AuthenticateService {
  public isLoggedInSubject: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
  private handleError: HandleError;
  
  constructor(private http: HttpClient,
    private config: ProjectConfigService,
              private httpErrorHandler: HttpErrorHandler,
              private jwtService: JwtService) {
      
      this.handleError = httpErrorHandler.createHandleError('/login');
  }

  public authenticateInternal(username: string, password: string) {
    const data = { username, password };
    return this.http.post(`/conppwPadraoC9-war/login`, JSON.stringify(data), httpOptions)
        .pipe(
          tap(data => {
            this.log('/login', data)
          }, error => {
            console.log('Error')
            this.logError('/login', error)
          }),
          catchError(this.handleError<Response>('Carregar itens')));
  }

  public logout() {
    httpOptions['Authorization'] = this.jwtService.getToken();
    return this.http.post(`/conppwPadraoC9-war/api/v9/security/logout`,
      {}, httpOptions)
      .pipe(
        tap(data => {
          this.log('/login', data)
        }, error => {
          console.log('Error')
          this.logError('/login', error)
        }),
        catchError(this.handleError<Response>('Carregar itens')));
  }

  public getIsLoggedIn() {
    return this.isLoggedInSubject.asObservable();
  }

  public setIsLoggedIn(isLoggedIn: boolean) {
    this.isLoggedInSubject.next(isLoggedIn);
  }

  public getUsername():string {
    let value = this.jwtService.decodeToken();
    return value['login'];
  }
  
  /**
     * Handle Http operation that succeded.
     * @param operation - name of the operation that succeded
     * @param result - Result value to return as the observable
     */
    private log<T>(operation: string, result: T) {
      // TODO: better job of transforming error for user consumption
      console.log(`${operation} succeded`);
  
      // Let the app keep running by returning an empty result.
      //return of(result as T);
      return result;
    }

  /**
	 * Handle Http operation that failed.
	 * Let the app continue.
	 * @param operation - name of the operation that failed
	 * @param result - optional value to return as the observable result
	 */
  private logError(operation: string, result: HttpErrorResponse) {
    // TODO: send the error to remote logging infrastructure
    if (result.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', result.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${result.status}, ` +
        `body was: ${result.error}`);
    }

    // TODO: better job of transforming error for user consumption
    console.log(`${operation} failed: ${result.message}`);

    // Let the app keep running by returning an empty result.
    //return of(result as T);
    return throwError(
      'Something bad happened; please try again later.');
  }
}
