//import { BrowserModule } from '@angular/platform-browser';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';

import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HttpClient }    from '@angular/common/http';
import { TranslateModule, TranslateLoader, MissingTranslationHandler } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';

import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { NgbDropdownModule, NgbDatepickerModule, NgbPaginationModule, NgbTypeaheadModule, NgbButtonLabel, NgbButtonsModule } from '@ng-bootstrap/ng-bootstrap';
import { ToastrModule } from 'ngx-toastr';

import { AceiteAcordoListComponent } from './list/aceite-acordo-list.component';
import { AcordoService } from './services/acordo.service';

import { NgbdSortableHeader } from './directives/sortable.directive';
import { AceiteAcordoRoutingModule } from './aceite-acordo-routing.module';
import { PPWMissingTranslationHandler } from './util/missingTranslationHandler';
import { AceiteAcordoComponent } from './aceite-acordo.component';
import { HttpErrorHandler } from './services/http-error-handler.service';
import { JwtService } from './services/jwt.service';
import { AuthenticateService } from './services/authenticate.service';
import { ProjectConfigService } from './services/project.config.service';
import { environment } from '../environments/environment';

const localeStartingURL = environment.localeUrlFolder;
const localeFileType = environment.localeFileType;


// AoT requires an exported function for factories
export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http, localeStartingURL, localeFileType);
}

@NgModule({
  declarations: [
    AceiteAcordoComponent,
     AceiteAcordoListComponent,
     NgbdSortableHeader 
  ],
  imports: [
    CommonModule,
    FontAwesomeModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    //NgbModule,
    NgbDropdownModule,
    NgbDatepickerModule,
    NgbPaginationModule, 
    NgbTypeaheadModule,
    NgbButtonsModule,
    ToastrModule.forRoot() ,
    TranslateModule.forChild({
          missingTranslationHandler: {provide: MissingTranslationHandler, useClass: PPWMissingTranslationHandler},
          loader: { provide: TranslateLoader, useFactory: HttpLoaderFactory, deps: [HttpClient]},
            isolate: true
        }),
    AceiteAcordoRoutingModule
  ],
  providers:[JwtService, ProjectConfigService, HttpErrorHandler, AcordoService,AuthenticateService],
  bootstrap: [AceiteAcordoComponent]
})
export class AceiteAcordoModule {
  constructor( private config: ProjectConfigService ) {
      config.load()
        .subscribe((item)=>console.log('Module initialized'));
      
  }
}
