import { Injectable } from '@angular/core';
import { NgbDateStruct, NgbDateParserFormatter, NgbDate } from '@ng-bootstrap/ng-bootstrap';
import { TextFormatter } from './TextFormatters';

@Injectable({
    providedIn: 'root'
})
export class YYYYMMDDDateFormatter extends NgbDateParserFormatter {
    parse(value: string): NgbDateStruct {
        if (value == null)
            return null;
        console.log(value);
        const date = new NgbDate(parseInt(value.substr(0,4)).valueOf(), parseInt(value.substr(4,2)).valueOf(), parseInt(value.substr(6,2)).valueOf());
        return date;
    }
    format(date: NgbDateStruct): string {
        if (date == null)
            return null;
        console.log(`${TextFormatter.preencherZerosEsquerda(date.year.toString(),4)}${TextFormatter.preencherZerosEsquerda(date.month.toString(),2)}${TextFormatter.preencherZerosEsquerda(date.day.toString(),2)}`);
        return `${TextFormatter.preencherZerosEsquerda(date.year.toString(),4)}${TextFormatter.preencherZerosEsquerda(date.month.toString(),2)}${TextFormatter.preencherZerosEsquerda(date.day.toString(),2)}`;
        //console.log(`${date.year}${date.month}${date.day}`);
        //return `${date.year}${date.month}${date.day}`;
    }
}
