import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { AceiteAcordoComponent } from './aceite-acordo.component';
import { AceiteAcordoListComponent } from './list/aceite-acordo-list.component';

@NgModule({
  imports: [
      RouterModule.forChild([
        {path: '', component: AceiteAcordoComponent,
          children: [
            {path: '', component: AceiteAcordoListComponent}
          ]
        }
    ])
  ],
  exports: [RouterModule]
})
export class AceiteAcordoRoutingModule { }
