import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class TextFormatter {
    static removeCaracter(texto: string, caracterExcl: string): string {
        return TextFormatter.trocaCaracter(texto, caracterExcl, "");
    }

    static trocaCaracter(texto: string, strAntiga: string, strNova: string): string {

        var textoAux: string = '';
        var found: Boolean = false;

        var tamTexto: number = texto.length;
        var tamCaracter: number = strAntiga.length;

        for (var i: number = 0; i < tamTexto; i++) {
            if (texto.charAt(i) == strAntiga.charAt(0)) {
                found = true;
                for (var j: number = 0; j < tamCaracter; j++) {
                    if (!(texto.charAt(i + j) == strAntiga.charAt(j))) {
                        found = false;
                        break;
                    }
                }

                if (found) {
                    textoAux += strNova;
                    i = i + (tamCaracter - 1);
                    continue;
                }
            }
            textoAux += texto.charAt(i);
        }
        return textoAux;
    }

    static preencherZerosEsquerda(texto : string, tamanho : number): string{
        var result : string;
        result = texto;
        while (result.length < tamanho){
            result = "0" + result;
        }
        return result;
    }
    
    static preencherEspacosEsquerda(texto : string, tamanho : number): string{
        var result : string;
        if (texto != null){
            result = texto;                		
        }else{
            result = " ";
        }
        if (result.length > tamanho){
            result = result.substring(0,tamanho);
        } 
        while (result.length < tamanho){
            result = " " + result;
        }
        return result;
    }

    static preencherEspacosDireita(texto : string, tamanho : number): string{
        var result : string;
        if (texto != null){
            result = texto;                		
        }else{
            result = " ";
        }
        if (result.length > tamanho){
            result = result.substring(0,tamanho);
        } 
        while (result.length < tamanho){
            result = result + " ";
        }
        return result;
    }
    
    static preencherCaracterEsquerda(texto : string, tamanho : number, caracter : string): string{
        var result : string;
        if (texto != null){
            result = texto;                		
        }else{
            result = caracter;
        }
        if (result.length > tamanho){
            result = result.substring(0,tamanho);
        } 
        while (result.length < tamanho){
            result = caracter + result;
        }
        return result;
    }
    
    static preencherCaracterDireita(texto : string, tamanho : number, caracter : string): string{
        var result : string;
        if (texto != null){
            result = texto;                		
        }else{
            result = caracter;
        }
        if (result.length > tamanho){
            result = result.substring(0,tamanho);
        } 
        while (result.length < tamanho){
            result = result + caracter;
        }
        return result;
    }
}
