import { Component, OnInit, QueryList, ViewChildren } from '@angular/core';

import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';

import { Observable } from 'rxjs';

import { AcordoService } from '../services/acordo.service';

import { Acordo } from '../models/acordo.model';
import { LovVals } from '../models/lovvals.model';

import { NgbDateStruct, NgbDate ,NgbDatepickerI18n, NgbDateParserFormatter, NgbDateAdapter, NgbInputDatepicker, NgbDateNativeAdapter } from '@ng-bootstrap/ng-bootstrap';

import { NgbdSortableHeader, SortEvent } from '../directives/sortable.directive';
import { faSort, faPlus, faTrashAlt, faPencilAlt, faSearch, faTimesCircle, faPaperPlane, faCalendarAlt, faCheck } from '@fortawesome/free-solid-svg-icons';

import { I18n, CustomDatepicker } from '../util/CustomDatepicker';
import { TextFormatter } from '../util/TextFormatters';
import { DateFormatters } from '../util/DateFormatters';
import { BrDateFormatter } from '../util/BrDateFormatters';
import { JwtService } from '../services/jwt.service';
import { AuthenticateService } from '../services/authenticate.service';
