@Component({
  selector: 'aceite-acordo-list',
  templateUrl: './aceite-acordo-list.component.html',
  styleUrls: ['./aceite-acordo-list.component.scss'],
  providers: [I18n, { provide: NgbDatepickerI18n, useClass: CustomDatepicker },
            { provide: NgbDateParserFormatter, useClass: BrDateFormatter }, 
            AcordoService, AuthenticateService]
})
export class AceiteAcordoListComponent implements OnInit {
  title = '';
  resourceName = '/';
  faPlus = faPlus;
  faPencilAlt = faPencilAlt;
  faTrashAlt = faTrashAlt;
  faSearch = faSearch;
  faSort = faSort;
  faCalendarAlt = faCalendarAlt;
  faCheck = faCheck;


  tiposStatusAceite$: Observable<LovVals[]>;
  loading$: Observable<boolean>;
  acordos$: Observable<Acordo[]>;
  
  //selectedAcordo$: Observable<Acordo>;
  selectedStatus$: Observable<string>;
  //selectedDataIni$: Observable<NgbDateStruct>;
  //selectedDataFim$: Observable<NgbDateStruct>;
  

  static LOCALE_KEY = 'currentLocale';

  @ViewChildren(NgbdSortableHeader) headers: QueryList<NgbdSortableHeader>;
  
  constructor(
              private  authService: AuthenticateService,
              private route: ActivatedRoute,
              private router: Router,
              private acordoService: AcordoService<Acordo>,
              private toastr: ToastrService,
              public translate: TranslateService) {
    this.acordos$ = acordoService.items$;
    this.loading$ = acordoService.loading$;
    //this.selectedAcordo$ = acordoService.selectedItem$;

    this.resourceName = '/'+ acordoService.getResourceName();

    translate.setDefaultLang('pt');
    const currentLocale =  sessionStorage.getItem(AceiteAcordoListComponent.LOCALE_KEY);
    
    translate.setDefaultLang(currentLocale);

  }

  get page() { return this.acordoService.page; }
  get pageSize() { return this.acordoService.pageSize; }
  get status() { return this.acordoService.status; }
  get periodoInicial() { return this.acordoService.periodoInicial; }
  get periodoFinal() { return this.acordoService.periodoFinal; }
  set page(page: number) { this.acordoService.page =  page;}
  set pageSize(pageSize: number) { this.acordoService.pageSize =  pageSize; }
  set status(status: string) {  this.acordoService.status = status; }
  set periodoInicial(periodoInicial: NgbDateStruct) {  this.acordoService.periodoInicial = periodoInicial; }
  set periodoFinal(periodoFinal: NgbDateStruct) {  this.acordoService.periodoFinal = periodoFinal; }

  onSort({ column, direction }: SortEvent) {
    // resetting other headers
    this.headers.forEach(header => {
      if (header.sortable !== column) {
        header.direction = '';
      }
    });

    // sorting items
    this.acordoService.sortColumn = column;
    this.acordoService.sortDirection = direction;
  }

  ngOnInit() {
   // this.auth.getUsername();
  }
  
  salvarAceiteAcordo(): void{
    this.acordoService.save().subscribe(
      (retorno) => {
        if(retorno != null) //this.router.navigate([this.goBackLink]);
          console.log(retorno);
      }
  );
  }
  
  clear(): void {
    this.status = 'Pendente';
    this.periodoInicial = new NgbDateNativeAdapter().fromModel(new Date());
    this.periodoFinal = new NgbDateNativeAdapter().fromModel(new Date());
    this.headers.forEach(header => {
        header.direction = '';
    });
  }

	efetuarPesquisa():void{

    //let arrIni = this.periodoInicial.split("/");
    let perDataIni = NgbDate.from(this.periodoInicial);
    //let arrFim = this.periodoFinal.split("/");
    let perDataFim = NgbDate.from(this.periodoFinal);
    
    console.log(`DataIni > Datafim? : ${perDataIni.before(perDataFim)}`);
    
    /*if(this.acordoService.validItem(this.content.codigoGrupo,this.content.codigoCriterioRemuneracao,this.content.tipoComissao,this.content.dataInicioVigencia,this.content.dataFimVigencia,this.content.faixaInicial,this.content.faixaFinal) === 1 ){
    }else{
        let msg =  `Faixa sobreposta em um periodo vigente`; 
        this.toastr.error(msg, "Erro:");
    }*/
    
		if(this.validaFiltro(perDataIni,perDataFim)){
      this.acordoService.loadAcordoList();
		}
	}

	/**
	 * Valida o Filtro
	 **/
	validaFiltro(dataI:NgbDate ,dataF:NgbDate):boolean{
	
		if(dataI === null){
			let msg =  `PerÃ­odo Inicial deve ser uma data vÃ¡lida`; 
      return false;
		}
		else if(dataF === null){
			let msg =  `PerÃ­odo Final deve ser uma data vÃ¡lida`; 
      return false;
    }
		else if(dataI.after(dataF)){
      let msg =  `PerÃ­odo inicial nÃ£o pode ser maior que o periodo final`; 
      this.toastr.error(msg, "Erro:");
			return false;
		}

		return true;
	}

  buscar() {
      this.acordoService.loadAcordoList();
  }

  checkAceite(nrAcordo: string){
    let collid:string = this.authService.getUsername();
    //let selected:boolean = this.acordoService.itemSelecionado(acordo.nrAcordo);
    //if (selected && !acordo.flagAceite)
    
    this.acordoService.changeUpdatedList(nrAcordo, 'nrAcordo', collid);

  }
}
