function compare(v1, v2) {
  return v1 < v2 ? -1 : v1 > v2 ? 1 : 0;
}

function sort<T>(items: T[], column: string, direction: string): T[] {
  if (direction === '') {
    return items;
  } else {
    return [...items].sort((a, b) => {
      const res = compare(a[column], b[column]);
      return direction === 'asc' ? res : -res;
    });
  }
}

function matches(acordo: any, text: string, status: boolean, periodoInicial: string, periodoFinal: string, pipe: PipeTransform) {
  const term = text.toLowerCase();
  let item: Acordo = acordo as Acordo;
  return (item.nrAcordo.toLowerCase().includes(term) && item.flagAceite === status &&
    (item.dtCriacaoAcordo.toString().includes(periodoInicial)
      || item.dtCriacaoAcordo.toString().includes(periodoFinal)));
}

function fullMatch(acordo: any, numeroAcordo: string) {
  let item: Acordo = acordo as Acordo;
  return (item.nrAcordo === numeroAcordo);
}

function validItemB(acordo: any, codigoAcordo: string, status: boolean) {
  let item: Acordo = acordo as Acordo;

  if (item.nrAcordo.includes(codigoAcordo)) {
    if (item.flagAceite === status) {
      return item;
    }
  }

  return (item.nrAcordo === codigoAcordo && item.flagAceite === status);

}
