  changeUpdatedList(itemValue: string, itemKey: string, username: string): void {
    let changedIndex = this.updatedItems.findIndex(value => value[itemKey] == itemValue);

    if (changedIndex < 0) {
      let changedItem = this.listaItems.find(value => value[itemKey] == itemValue);
      var clone = { ...changedItem };
      clone['gestorAceite'] = username;
      clone['flagAceite'] = (changedItem['flagAceite']) ? 'S' : 'N';
      this.updatedItems.push(clone);
    } else {
      this.updatedItems.splice(changedIndex, 1);
    }

  }


